html {
  margin: 0;
  padding: 0; }

body {
  margin: 0;
  padding: 0;
  font-family: "Yu Gothic", "游ゴシック", YuGothic, "游ゴシック体", sans-serif;
  font-weight: 500;
  color: #fff;
  line-height: 2.0;
  letter-spacing: 0.1em;
  overflow-x: hidden; }

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;

  &:before, &:after {
    -webkit-box-sizing: border-box;
    box-sizing: border-box; } }

h2, h3, h4, h5 {
  margin: 0;
  padding: 0; }

img {
  width: 100%;
  vertical-align: bottom; }

figure {
  margin: 0; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

ul, li, dl, dt, dd {
  margin: 0;
  padding: 0;
  list-style: none; }

a {
  text-decoration: none;
  transition: all .6s ease;

  &:hover {
    transition: all .6s ease; } }

.content-wrapper {
  width: 95%;
  margin: 0 auto;
  max-width: 1000px; }

section h2 {
  font-size: 2rem; }

/* ヘッダー部分 */

.header {
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -ms-flex-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100px;
  position: absolute;
  z-index: 1;

  h1 {
    margin: 0 0 0 25px;
    font-size: 2.5rem;
    color: #fff; }

  nav ul {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    align-items: center;
    font-size: 1.2rem;

    li {
      margin: 0 20px; } } }

header nav li a {
  color: #fff;

  &:hover {
    opacity: 0.6; } }

.header nav li.phone {
  margin: 0 30px 0 10px;
  Padding: 0 25px;
  border: 1px solid #fff;
  line-height: 50px;
  display: block;
  color: #fff; }

@media screen and (max-width: 1199px) {
  .hamburger {
    display: block;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    width: 30px;
    height: 26px;
    cursor: pointer; }

  header nav {
    display: none; }

  .nav-button {
    display: inline-block;

    span {
      display: inline-block;
      position: absolute;
      left: 0;
      width: 100%;
      height: 4px;
      background-color: #fff;

      &:nth-of-type(1) {
        top: 0; }

      &:nth-of-type(2) {
        top: 11px; }

      &:nth-of-type(3) {
        bottom: 0; } }

    position: relative;
    width: 30px;
    height: 26px; } }

/* top部分 */

.mainvisual {
  position: relative;

  img {
    height: 700px;
    width: 100vw;
    object-fit: cover;
    object-position: 0 15%;
    -webkit-clip-path: polygon(0 0, 100% 0%, 100% 85%, 0% 100%);
    clip-path: polygon(0 0, 100% 0%, 100% 85%, 0% 100%); }

  h2 {
    position: absolute;
    top: 50%;
    right: 10%;
    transform: translateY(-50%);
    z-index: 1;
    font-size: 5vw;
    color: #fff; } }

@media screen and (max-width: 1199px) {
  .mainvisual {
    position: relative;

    img {
      height: 700px;
      width: 100vw;
      object-fit: cover;
      object-position: 0 15%;
      -webkit-clip-path: polygon(0 0, 100% 0%, 100% 85%, 0% 100%);
      clip-path: polygon(0 0, 100% 0%, 100% 85%, 0% 100%); }

    h2 {
      position: absolute;
      top: 50%;
      right: 10%;
      transform: translateY(-50%);
      z-index: 1;
      font-size: 5vw;
      color: #fff; } } }

@media screen and (max-width: 767px) {
  .mainvisual h2 {
    transform: translateY(-50%) skewY(+5deg);
    top: 70%;
    right: 10%;
    font-size: 10vw; } }

/* topics */

.topics {
  padding: 120px;

  .content-wrapper {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex; }

  .heading {
    color: #333;
    width: 35%; }

  .content {
    width: 65%;

    table {
      width: 100%;
      margin: 0 0 25px 0; }

    th {
      color: #333;
      padding: 20px 2em 20px 0;
      border-bottom: 1px dotted; }

    td {
      color: #333;
      border-bottom: 1px dotted; }

    tr:last-of-type {
      th, td {
        border-bottom: none; } }

    .btn {
      margin: 0;

      a {
        display: block;
        font-size: 0.8rem;
        color: #000;
        border: 1px solid #000;
        width: 200px;
        line-height: 60px;
        text-align: center;
        margin: 0 0 0 auto;

        &:hover {
          background: #333;
          color: #fff; } } } } }

@media screen and (max-width: 1199px) {
  .topics {
    padding: 50px 5vw;

    .content-wrapper {
      display: block; }

    .heading {
      width: 100%;

      h2 {
        text-align: center;
        margin-bottom: 5vw; } }

    .content {
      width: 100%; } } }

@media screen and (max-width: 767px) {
  .topics {
    padding: 50px 0;
    width: 90%;
    margin: 0 auto;

    .content .btn a {
      width: 35%;
      min-width: 200px;
      margin: 0 auto; } } }

@media screen and (max-width: 479px) {
  .topics .content {
    th {
      display: block;
      width: 100%;
      text-align: left;
      padding: 1.25em 0 0 0;
      border-bottom: 0; }

    td {
      display: block;
      width: 100%; } } }

/* info */

.info {
