/* 募集欄 */

.recruit {
  padding: 10% 0;
  position: relative;

  .heading {
    color: #333;
    width: 100%;
    margin: 0 auto;
    padding: 0 0 40px;
    text-align: left; }

  h2 {
    font-size: 2rem;
    margin-bottom: 5vw; }

  &::after {
    content: "";
    background-color: #f3f3f3;
    height: 150%;
    width: 40%;
    position: absolute;
    top: -10%;
    left: 30%;
    transform-origin: left top;
    transform: skewX(-30deg);
    z-index: -1; }

  ul {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -ms-flex-pack: justify;
    -webkit-justify-content: space-between;
    justify-content: space-between;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    transform: skewY(2deg);

    li {
      width: 48%;
      position: relative;

      figure::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding-top: 43%;
        background: rgba(50, 50, 50, 0.7);
        z-index: 1;
        transition: all .6s ease; }

      a:hover figure::after {
        background: rgba(50, 50, 50, 0.4);
        transition: all .6s ease; } } }

  li span {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) skewY(-2deg);
    z-index: 2;
    color: #fff;
    font-size: 2rem;
    text-align: center; } }

@media screen and (max-width: 767px) {
  .recruit {
    .heading {
      text-align: center; }

    ul {
      transform: none; }

    li span {
      transform: translate(-50%, -50%);
      font-size: 1.75rem; } } }

@media screen and (max-width: 479px) {
  .recruit ul li {
    width: 90%;
    margin: 0 auto 20px auto; } }

